/* { の前にスペースがないとエラー */
.item{
	margin: 1px;
}


/* ↑ 空行が2行になるとエラー */

.box {
	/* 0 の場合は単位あるとエラー */
	margin: 0px;

	/* カラーコード 3文字に短縮するとエラー */
	background-color: #fff;

	/* 文字列 ダブルクオートじゃないとエラー */
	background-image: '../img/test.jpg';
	> .list {
		/* ↑ ネストして書くとき上に1行空いてないとエラー */

		/* ↓ セミコロンが無かったり重複してたらエラー */
		height: 10px;;
		margin: 10px
	}
}

/* 何もスタイルが書いてないとエラー */
.wrap {
}

.container {
	width: 10px;
	height: 10px;

	/* プロパティが重複しているとエラー */
	width: 3px;
}

/* 最後に空行がないとエラー */